import React from 'react'
import {useState} from 'react'

const App = () => {

  const [count, setCount] = useState(0);
  return (
    <div className='main'>
     
      <h1>Counter</h1>
      <button onClick={() => setCount(count - 1)}>-</button>
           <h2>{count}</h2>
           <button onClick={() => setCount(count + 1)}>+</button>
  
    </div>
  )
}

export default App
